import React from 'react'
import { Link, withRouter } from 'react-router-dom'
import logo from '../../assets/logo.png'
import './index.scss'

const Header = (props) => {
    return (
        <div className='header-row'>
             <Link to='/'>
                 <div className='logo'>
                    <img src={logo} alt='logo' />
                </div>
             </Link>
            
            <div className='registration'>
            {
                (props.location.pathname === '/registration' && <p>–£–∂–µ –µ—Å—Ç—å –∞–∫–∫–∞—É–Ω—Ç?</p>) 
                || (props.location.pathname === '/' && <p>–£ –≤–∞—Å –Ω–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞?</p>)
                || (props.location.pathname === '/confirmemail' && <p style={{display: "block"}}>–í—ã–π—Ç–∏</p>)
                || (props.location.pathname === '/notreceiveemail' && <p style={{display: "block"}}>–í—ã–π—Ç–∏</p>)
            }
            {
                (props.location.pathname === '/registration' && <Link to='/'><button>–í–æ–π—Ç–∏</button></Link>) 
                || (props.location.pathname === '/' && <Link to='/registration'><button>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</button></Link>)
                || (props.location.pathname === '/confirmemail' && <button style={{display: "none"}}>–í–æ–π—Ç–∏</button>)
            } 
            </div>
        </div>
    )
}

export default withRouter(Header)
