import React, { useState } from 'react'
import './index.scss'
import Header from '../../components/Header'
import FacebookLogo from '../../assets/FacebookLogo.svg'
import GoogleLogo from '../../assets/GoogleLogo.svg'
import { Formik, Form, useField } from 'formik';
import * as Yup from 'yup';
import { Redirect } from 'react-router-dom'

const RegistrationForm = () => {

    const [toNextPage, settoNextPage] = useState(false);
    const [input, setInput] = useState(false);

    const MyTextInput = ({...props }) => {
        const [field, meta] = useField(props);
        return (
            <>
            <input className={`${meta.touched && meta.error ? "error-input text-input" : "text-input"}`} {...field} {...props} />
            {meta.touched && meta.error ? (
                <div className="error">{meta.error}</div>
            ) : null}
            </>
        );
    };

    const authSchema = Yup.object().shape({
        name: Yup.string('')
                    .required('–í–≤–µ–¥–∏—Ç–µ –∏–º—è'),
        email: Yup.string('')
                    .email('–ù–µ–≤–µ—Ä–Ω—ã–π –µmail')
                    .matches("^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9.+-]+.com$", '–í–æ–∑–º–æ–∂–Ω–æ –≤—ã –æ—à–∏–±–ª–∏—Å—å –≤ —É–∫–∞–∑–∞–Ω–∏–∏ –ø–æ—á—Ç–æ–≤–æ–≥–æ —Å–µ—Ä–≤–∏—Å–∞')
                    .required('–í–≤–µ–¥–∏—Ç–µ email'),
        password: Yup.string('')
                    .required('–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å')              
    });

    const showInput = () => {
        setInput(true);
    }

    return (
        <>
          <Header />  
          <div className='auth-main'>
               <h2>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
               <h3>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–π—Å—è –∏ –ø–æ–ª—É—á–∏ –¥–æ—Å—Ç—É–ø –∫ –∞–Ω–∞–ª–∏—Ç–∏–∫–µ –∞–∫–∫–∞—É–Ω—Ç–æ–≤.</h3>
               <div className='auth-buttons'>
                   <button className='facebook-btn'>
                       <img src={FacebookLogo} alt="facebook logo" />
                       –í–æ–π—Ç–∏ —á–µ—Ä–µ–∑ Facebook
                    </button>
                   <button className='google-btn'>
                        <img src={GoogleLogo} alt="google logo" />
                        –í–æ–π—Ç–∏ —á–µ—Ä–µ–∑ Google
                    </button>
               </div>
               <p>–∏–ª–∏</p>
                <Formik
                    initialValues={{
                    name: '',
                    email: '',
                    password: '',
                    promocode: '',
                    }}
                    validationSchema={authSchema}
                    onSubmit={(values, { setSubmitting, resetForm}) => {
                            settoNextPage(true);
                            console.log(values);
                            setSubmitting(false);
                            resetForm(); 
                }}
                >
                    <Form className='auth-form' autoComplete="off">
                        <MyTextInput
                            name="name"
                            type="name"
                            placeholder="–ò–º—è"
                        />
                        <br/>
                        <MyTextInput
                            name="email"
                            type="email"
                            placeholder="E-mail"
                        />
                        <br/>
                        <MyTextInput
                            name="password"
                            type="password"
                            placeholder="–ü–∞—Ä–æ–ª—å"
                        />
                        <br/>
                        {input ? 
                        <MyTextInput
                            name="promocode"
                            type="promocode"
                            placeholder="–ü—Ä–æ–º–æ–∫–æ–¥"
                        />
                        : <button className='forgot-password-btn' onClick={showInput}>–£ –º–µ–Ω—è –µ—Å—Ç—å –ø—Ä–æ–º–æ–∫–æ–¥</button>
                        }
                        {toNextPage ?
                        <Redirect to='/confirmemail'>
                            <button type="submit" className='submit-btn' >–°–æ–∑–¥–∞—Ç—å –∞–∫–∫–∞—É–Ω—Ç</button>
                        </Redirect> 
                        : <button type="submit" className='submit-btn' >–°–æ–∑–¥–∞—Ç—å –∞–∫–∫–∞—É–Ω—Ç</button>
                        }
                        <h5 className='registration-p'>–°–æ–∑–¥–∞–≤–∞—è –∞–∫–∫–∞—É–Ω—Ç, —è —Å–æ–≥–ª–∞—Å–µ–Ω<button className='forgot-password-btn'>—Å —É—Å–ª–æ–≤–∏—è–º–∏ –æ—Ñ–µ—Ä—Ç—ã</button></h5>
                    </Form>
                </Formik>
            </div> 
        </>
    )
}

export default RegistrationForm
